import React from "react";
import {
  TableHead,
  TableRow,
  Checkbox,
  TableCell,
  TableSortLabel
} from "@material-ui/core";

const headRows = [
  {
    id: "model",
    numeric: true,
    disablePadding: true,
    label: "Model명"
  },
  {
    id: "trained_at",
    numeric: true,
    disabledPadding: true,
    label: "Model 학습 일시"
  },
  {
    id: "state",
    numeric: true,
    disabledPadding: true,
    label: "Model 상태"
  }
];
const ModelTableHead = props => {
  console.log("ModelTableHead", props);
  const {
    onSelectAllClick,
    order,
    orderBy,
    numSelected,
    rowCount,
    onRequestSort
  } = props;

  const createSortHandler = property => event => {
    onRequestSort(event, property);
  };

  return (
    <TableHead>
      <TableRow>
        <TableCell padding="checkbox">
          <Checkbox
            indeterminate={numSelected > 0 && numSelected < rowCount}
            checked={numSelected === rowCount}
            onChange={onSelectAllClick}
            inputProps={{ "aria-label": "모델 전체 선택" }}
          />
        </TableCell>
        {headRows.map(row => (
          <TableCell
            key={row.id}
            align={row.numeric ? "center" : "right"}
            padding={row.disablePadding ? "none" : "default"}
            sortDirection={orderBy === row.id ? order : false}
          >
            <TableSortLabel
              active={orderBy === row.id}
              direction={order}
              onClick={createSortHandler(row.id)}
            >
              {row.label}
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
};

export default ModelTableHead;
