import axios from "axios";

export const GET_MODEL_LIST_URL = "/api/projects";

export const GET_MODEL_LIST = "[MODEL APP]GET MODEL LIST";

export function getModelList(project_id, params) {
  // debugger;
  var queryString = Object.keys(params)
    .map(function(key) {
      return key + "=" + params[key];
    })
    .join("&");

  return axios.get(
    GET_MODEL_LIST_URL + "/" + project_id + "/" + "models" + "?" + queryString
  );
}

export function activeModel(project_id, model_nm) {
  // debugger;
  return axios.put(
    GET_MODEL_LIST_URL +
      "/" +
      project_id +
      "/" +
      "models" +
      "/" +
      model_nm +
      "/tags/" +
      "production"
  );
}

export function removeModel(project_id, model_nm) {
  // debugger;
  return axios.delete(
    GET_MODEL_LIST_URL +
      "/" +
      project_id +
      "/" +
      "models" +
      "/" +
      model_nm
  );
}

export function createModel(project_id, token) {
  // debugger;

  // const queryString = {
  //   "api_token": JSON.stringify(token)
  // }

  return axios.post(
    GET_MODEL_LIST_URL + "/" + project_id + "/" +  "models" + "/" + "jobs", token  
  );
}
