const ModelTableToolbar = props => {
  const classes = useToolbarStyles();
  const { numSelected, onRefresh, onActive, onRemove, onCreate } = props;

  return (
    <Toolbar
      className={clsx(classes.root, {
        [classes.highlight]: numSelected > 0
      })}
    >
      <div className={classes.title}>
        {numSelected > 0 ? (
          <Typography color="inherit" variant="subtitle1">
            {numSelected} 개 모델이 선택되었습니다
          </Typography>
        ) : (
          <Typography variant="h6" id="tableTitle">
            학습 Model 리스트
          </Typography>
        )}
      </div>
      <div className={classes.spacer} />
      <div className={classes.actions}>
        {numSelected > 0 ? (
          numSelected === 1 ? (
            <>
              <Tooltip title="모델 활성화">
                <IconButton aria-label="Active Model" onClick={onActive}>
                  <DoubleArrow />
                </IconButton>
              </Tooltip>
              <Tooltip title="모델 삭제">
                <IconButton aria-label="Delete Model" onClick={onRemove}>
                  <DeleteIcon />
                </IconButton>
              </Tooltip>
            </>
          ) : (
            <>
              <Tooltip title="모델 삭제">
                <IconButton aria-label="Delete Model" onClick={onRemove}>
                  <DeleteIcon />
                </IconButton>
              </Tooltip>
            </>
          )
        ) : (
          <>
            <Tooltip title="재조회">
              <IconButton aria-label="Refresh Model" onClick={onRefresh}>
                <RefreshIcon />
              </IconButton>
            </Tooltip>
          </>
        )}
      </div>
    </Toolbar>
  );
};

export default ModelTableToolbar;
